.wrapper_box{
    display: grid;

    grid-template-areas:
    'o c';

    grid-template-columns: 224px 1fr;
}
.wrapper_box__organization{
    grid-area: o;

    background-color: #F7F7F7;
    
    height: 900px;
    width: 224px;
}
.wrapper_box__listAgents{
    grid-area: c;

    height: 900px;
}